import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import "../../styles/HomePage.css";
import LoginPageModal from '../LoginPageModal.tsx'; //  LoginPageModal  Sidebar

const Homepage: React.FC = () => {
  const [isLoginModalVisible, setIsLoginModalVisible] = useState(false);
  const navigate = useNavigate();

  // 
  const checkLoginStatus = () => {
    const token = localStorage.getItem('token');
    return token !== null; //  token 
  };

  // / LoginModal
  const toggleLoginModal = () => {
    setIsLoginModalVisible(!isLoginModalVisible);
  };

  // 
  const handleButtonClick = (action: string) => {
    if (!checkLoginStatus()) {
      //   LoginModal
      toggleLoginModal();
    } else {
      //   Product
      if (action === 'addProduct') {
        navigate('/add-products');
      } else {
        //  
        console.log('Handling other actions');
      }
    }
  };

  return (
    <div className="container">
      <header className="header">
        <h1 className="title"></h1>
        <p className="subtitle"></p>
      </header>
      <main className="main">
        <div className="card">
          <h2 className="home-h2"></h2>
          <p className="home-p"></p>
          <button className="btn" onClick={() => handleButtonClick('addProduct')}>
            
          </button>
        </div>
        <div className="card">
          <h2 className="home-h2"></h2>
          <p className="home-p"></p>
          <button className="btn" onClick={() => handleButtonClick('manageProducts')}>
            
          </button>
        </div>
        <div className="card">
          <h2 className="home-h2"></h2>
          <p className="home-p"></p>
          <button className="btn" onClick={() => handleButtonClick('viewReports')}>
            
          </button>
        </div>
      </main>

      {/* Login Modal */}
      {isLoginModalVisible && <LoginPageModal isVisible={isLoginModalVisible} onClose={toggleLoginModal} />}
    </div>
  );
};

export default Homepage;
